<template>
  <div class="box_right">
    <div class="box_border">
      <div class="home_block">
        <!--  -->
        <p class="Awords"><b>,</b></p>
        <p style="text-align: right;color: #fff"></p>
        <hr style="color:#fff;margin-bottom: 0;">
        <!--  -->
        <div class="updatas col-12 col-md-8">
          <p style="background-color: #161616;color: #fff;margin: 0;line-height: 40px;font-size: 22px;"><b></b></p>
          <ul>
            <li v-for="item in logs" :key="item.date">
            <table style="width: 100%;">
                <thead style="background-color: #666">
                    <tr><th>{{ item.date }}</th></tr>
                </thead>
                <tbody>
                    <tr v-for="(logs,index) in item.imformation" :key="index"><th style="color:#ebebeb;padding-left: 20px;">&nbsp;{{ logs }}</th></tr>
                </tbody>
            </table>
            </li>
          </ul>
          <div class="inpt_box" type="text">
            <div class="time-now">
              <div>{{ currentDate }}</div>
              <div>{{ currentTime }}</div>
            </div>
            <input id="updateText" type="text" class="text_box" placeholder="">
            <a class="btn text_button" @click="updateLogs"></a>
          </div>
        </div>
        <!--  -->
        <div class="feedback-operator col-md-4">
            <div class="questions"><p style="text-align: center;"></p></div>
            <div class="answers">
                <button class="yes"><br>num</button>
                <button class="no"><br>num</button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomePage',
  data () {
    return {
      logs: [{
        date: '2023.11.30',
        imformation: ['', ',JS', '', ',JS', 'GPT']
      }, {
        date: '2023.11.29',
        imformation: ['', '', 'Play', '']
      }, {
        date: '2023.11.28',
        imformation: ['Play/Pause', '', '']
      }, {
        date: '2023.11.27',
        imformation: ['godot', '', '', '']
      }, {
        date: '2023.11.26',
        imformation: ['', 'vedio', '', 'css', '', '', '']
      }, {
        date: '2023.11.25',
        imformation: ['', 'top-nav()', 'bootstrap', 'Vue()', 'LOGO', '']
      }
      ],
      currentDate: '',
      currentTime: ''
    }
  },
  methods: {
    formattedDate () {
      const date = new Date()
      const year = date.getFullYear()
      const month = (date.getMonth() + 1).toString().padStart(2, '0')
      const day = date.getDate().toString().padStart(2, '0')

      // yyyy.MM.dd
      const formattedDate = `${year}.${month}.${day}`

      return formattedDate
    },
    formattedTime () {
      const date = new Date()
      const hours = date.getHours().toString().padStart(2, '0')
      const minutes = date.getMinutes().toString().padStart(2, '0')
      const seconds = date.getSeconds().toString().padStart(2, '0')

      // hh.mm.ss
      const formattedTime = `${hours}:${minutes}:${seconds}`

      return formattedTime
    },
    updateLogs () {
      var date = this.currentDate
      var texts = document.getElementById('updateText').value
      var hasdate = this.logs.some(item => item.date === date)
      if (hasdate) {
        var changeitem = this.logs.find(item => item.date === date)
        changeitem.imformation.push(texts)
      } else {
        var newitem = { date: date, imformation: [texts] }
        this.logs.unshift(newitem)
      }
    }
  },
  mounted () {
    setInterval(() => {
      this.currentDate = this.formattedDate()
      this.currentTime = this.formattedTime()
    }, 500)
  }
}
</script>

